import { Heading1Data } from '@ali/4ever-plugin-heading';
import { Paragraph } from '@ali/4ever-plugin-paragraph'
import { Text, Block, BlockChildren } from '@ali/4ever-cangjie';

export type SubjectHeading = Block<Heading1Data, BlockChildren>;

export type Subject = Block<Heading1Data, Block[]>;

/**
 * 判断节点是否是议题标题
 * @param node
 * @returns
 */
export function isSubjectHeading(node: any): node is SubjectHeading {
  return Block.isBlock(node) && node.type === 'heading-1' && !!node.data.subjectPr;
}

/**
 * 判断节点是否是议题组
 */
export function isSubject(node: any): node is Subject {
  return Block.isBlock(node) && node.type === 'subject-group';
}

export interface SubjectProps {
  key?: string;
  data: Heading1Data;
  nodes?: Block[];
}

export function createSubject(props: SubjectProps): Subject {
  return Block.create<Heading1Data, Block[]>({
    ...props,
    type: 'subject-group',
  });
}

/**
 * 判断节点是否是空节点
 * @param node
 * @returns
 */
export function isEmptyNode(node: Block): boolean {
  return (
    node.nodes.length === 0 ||
    (node.nodes.length === 1 &&
      Paragraph.isParagraph(node) &&
      Text.isEmptyText(node.nodes[0]))
  );
}

export function isNormalParagraph(node: Block): boolean {
  return Paragraph.isParagraph(node) && !node.data.list;
}

/**
 * 简单生成 uniqueId
 * @see https://stackoverflow.com/questions/105034/how-to-create-a-guid-uuid#answer-44078785
 * @returns
 */
export function uniqueId() {
  return Date.now().toString(36) + Math.random().toString(36).substring(2);
}
